* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}
ul{
  list-style: none;
  padding-left: 0;
}
#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-card__languages {
  overflow: hidden;
  padding-left: 0;
}
.country-card__language, 
.country-card__description{
  display: inline-block;
}

.country-card__language:not(:last-child)::after{
  content:",";
}
.country-name {
margin-left: 10px;
font-weight: 700;
}
.countries-list{
  display: flex;
}
.countries-list:not(:last-child){
  margin-bottom: 20px;
}
p,
.country-card__description{
  font-weight: 700;
}
span{
  font-weight: 400;
}